<template>
    <div class="store" v-loading="loading">
        <vue-scroll>
            <div class="store-settings">
                <el-form class="form" ref="form" :model="applyForm" :rules="rules" size="small">
                    <el-form-item>
                        <span class="title float-l">{{$L('店铺地址')}}:</span>
                        <a :href="applyForm.shareLink" target="_blank" class="link float-l mainFontColor">
                            {{ applyForm.shareLink}}
                        </a>
                        <span class="state mainFontColor float-l" @click="storeCodeFunc">
                    </span>
                    </el-form-item>
                    <el-form-item>
                        <span class="title">{{$L('使用独立域名')}}:</span>
                        <el-input v-model="applyForm.shopDomain" :placeholder="$L('类似 www.test.com 的域名')"></el-input>
                       
                        <p class="domain-tips">
                            *{{ $L('更换店铺域名后，请将CNAME地址解析到') }}，
                            <a :href="applyForm.bindDomain" target="_blank">
                            <span class="mainFontColor">
                                {{ applyForm.bindDomain }}
                            </span>
                            </a>
                            {{ $L('等待2个小时后即可访问您的店铺') }}！
                        </p>
                    </el-form-item>
                    <el-form-item>
                        <span class="title">{{$L('店铺LOGO')}}:</span>
                        <div class="logo-img">
                            <label class="img-box">
                                <img :src="applyForm.shopImg" class="img">
                                <input id="fileinp" type="file" name="CommentsPic" :placeholder="$L('上传图片')" multiple accept="image/png,image/jpeg,image/jpg" @change="upImgFunc"/>
                            </label>
                            <p class="tips">
                                <span>{{ $L('建议尺寸：200 x 200 像素小于120KB，支持jpg、gif、png格式') }}</span>
                            </p>
                        </div>
                    </el-form-item>
                    <el-form-item>
                        <span class="title">{{$L('店铺名称')}}:</span>
                        <el-input v-model="applyForm.shopName"></el-input>
                    </el-form-item>
                </el-form>
            </div>
            <div class="bottom-btns">
                <el-button @click="saveSettingFunc" type="primary" class="btn">{{ $L('保存') }}</el-button>
            </div>
            <el-dialog
                    :visible.sync="storeCode"
                    width="80%" :modal = true
                    :modal-append-to-body = 'false' append-to-body class="crash-dialog">
                <div slot="title" class="dialog-header" style="display: none;"></div>
                <div class="dialog-item">
                    <img :src="qrcodeLink" class="img">
                    <p class="title">{{ $L('店铺二维码') }}</p>
                </div>
                <div slot="footer" class="dialog-footer" style="display: none;"></div>
            </el-dialog>
        </vue-scroll>
    </div>
</template>

<script>
    export default {
        name: "storeSettings",
        data () {
            return {
                loading:true,
                errorType: 'error',
                errorTitle: this.$L('出错了'),
                errorTips: null,
                errorShow: false,
                applyForm:{
                    shopName:'',
                    shopImg:'',
                    shopDomain:'',
                    shareLink:'',
                    bindDomain:''
                },
                rules:{
                    shopName:[
                        {required: true}
                    ],
                },
                storeCode:false,
                qrcodeLink:'',
                uploadImg:[]
            }
        },
        inject : ['reload'],
        created () {
            this.getData();
        },
        methods : {
            showError(title, tips, type) {
                if (type === 'warning') {
                    this.errorType = 'warning';
                } else {
                    this.errorType = 'error';
                }
                if (title && title !== '') {
                    this.errorTitle = title;
                } else {
                    this.errorTitle = this.$L('出错了');
                }
                if (tips && tips !== '') {
                    this.errorTips = tips;
                } else {
                    this.errorTips = null;
                }
                this.loading = false;
                this.errorShow = true;
            },
            getData() {
                const _self = this;
                _self.loading = true;
                _self.$api('distributionshopSetting', { isMobile : 0} , function (reData) {
                    if (reData.flag === 1) {
                        _self.applyForm.shopName = reData.data.shopName;
                        _self.applyForm.shopImg = reData.data.shopImg;
                        _self.applyForm.shopDomain = reData.data.shopDomain;
                        _self.applyForm.shareLink = reData.data.shareLink;
                        _self.qrcodeLink = reData.data.qrcodeLink;
                        _self.applyForm.bindDomain = reData.data.bindDomain;
                        _self.loading = false;
                    } else if (reData.flag === -1) {
                        //登录超时了
                        _self.$confirm(_self.$L('请重新登录'), _self.$L('登录超时'), {
                            confirmButtonText: _self.$L('重新登录'),
                            cancelButtonText: _self.$L('取消'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.$router.push({name: 'login', query: {redirect: _self.$route.fullPath}});
                        }).catch(action => {
                            _self.$gotoWebHome();
                        });
                    } else {
                        //未知错误
                        _self.$confirm(_self.$L('数据加载失败'), _self.$L('出错了'), {
                            confirmButtonText: _self.$L('点击重试'),
                            cancelButtonText: _self.$L('返回首页'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.getData();
                        }).catch(action => {
                            _self.$router.push({name: 'distributorMain'});
                        });
                    }
                }, function (error) {
                    //网络请求错误
                    _self.$confirm(_self.$L('网络请求失败'), _self.$L('出错了'), {
                        confirmButtonText: _self.$L('点击重试'),
                        cancelButtonText: _self.$L('返回首页'),
                        type: 'error',
                        distinguishCancelAndClose: true
                    }).then(() => {
                        _self.getData();
                    }).catch(action => {
                        _self.$router.push({name: 'distributorMain'});
                    });
                });
            },
            //上传图片
            upImgFunc(event) {
                const _self = this;
                let files = event.target.files[0];
                let size = Math.floor(files.size / 1024);
                let type = files.type.substring(6);
                if (size > 2*1024*1024) {
                    _self.$Message.info('请选择2M以内的图片！');
                    return false;
                }
                if(!(type === 'png' || type === 'jpeg' || type === 'jpg')){
                    _self.$Message.info('请选择png、jpeg、jpg的图片格式！');
                    return false;
                }
                _self.uploadImg= {
                    'file':files
                };
                let reader = new FileReader();
                reader.readAsDataURL(files);
                reader.onload = function(evt){
                    _self.applyForm.shopImg = evt.target.result;
                }
            },
            saveSettingFunc(){
                const _self = this;
                _self.loading = true;
                if (_self.applyForm.shopName == '') {
                    _self.$message({
                        message: _self.$L('店铺名称不能为空!'),
                        type: 'error'
                    });
                    _self.loading = false;
                    return ;
                }
                // if (_self.applyForm.shopDomain == '') {
                //     _self.$message({
                //         message: _self.$L('域名不能为空!'),
                //         type: 'error'
                //     });
                //     _self.loading = false;
                //     return ;
                // }
                _self.applyForm.img = _self.uploadImg;
                let arr = {
                    shopName : _self.applyForm.shopName,
                    shopDomain : _self.applyForm.shopDomain,
                    BindLogo : _self.applyForm.img
                };
                let postData = _self.$objToFormData(arr);
                _self.$api('distributionShopSettingBtn', postData , function (reData) {
                    if (reData.flag === 1) {
                        _self.$message({
                            type: 'success',
                            message: _self.$L('保存成功!')
                        });
                        _self.getData();
                        _self.loading = false;
                    } else if (reData.flag === 0) {
                        _self.$message({
                            type: 'info',
                            message: reData.msg
                        });
                        _self.loading = false;
                    } else if (reData.flag === -1) {
                        //登录超时了
                        _self.$confirm(_self.$L('请重新登录'), _self.$L('登录超时'), {
                            confirmButtonText: _self.$L('重新登录'),
                            cancelButtonText: _self.$L('取消'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.$router.push({name: 'login', query: {redirect: _self.$route.fullPath}});
                        }).catch(action => {

                        });
                    } else {
                        //未知错误
                        _self.$confirm(_self.$L(reData.msg), _self.$L('出错了'), {
                            confirmButtonText: _self.$L('点击重试'),
                            cancelButtonText: _self.$L('返回首页'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.getData();
                        }).catch(action => {
                            if (action == 'cancel') {
                                _self.$gotoWebHome();
                            } else {
                                _self.getData();
                            }
                        });
                    }
                }, function (error) {
                    //网络请求错误
                    _self.$confirm(_self.$L('网络请求失败'), _self.$L('出错了'), {
                        confirmButtonText: _self.$L('点击重试'),
                        cancelButtonText: _self.$L('返回首页'),
                        type: 'error',
                        distinguishCancelAndClose: true
                    }).then(() => {
                        _self.getData();
                    }).catch(action => {
                        _self.$router.push({name: 'distributorUserOrder'});
                    });
                }, true);

            },
            storeCodeFunc(){
                this.storeCode = true;
            }
        }
    }
</script>

<style lang="scss" scoped>
    @import "../../../../assets/css/common/common.scss";
    .store-settings{
        background: #fff;
        .form{
            font-size: 12px;
            padding:15px 15px 60px;
            /deep/ .el-form-item{
                margin-bottom: 25px;
                .title{
                    display: inline-block;
                    padding-bottom: 10px;
                    line-height: 1.5;
                    width: 90px;
                }
                .el-form-item__content{
                    font-size: 12px;
                    overflow: hidden;
                }
                .el-input{
                    width: calc(100% - 90px);
                }
            }
            .link{
                width: calc(100% - 140px);
                line-height: 1.5;
                margin: 0 10px;
                word-break: break-word;
            }

            .state{
                background: url("../../../../assets/img/distributor/storecode.png") no-repeat center;
                background-size: cover;
                display: inline-block;
                height: 25px;
                width: 25px;
            }
            .domain-tips{
                color: #999;
                margin-top: 10px;
                line-height: 1.5;
            }
            .logo-img{
                .img-box{
                    display: inline-block;
                    width: 82px;
                    height: 82px;
                    vertical-align: middle;
                    border: 1px solid #999;
                    #fileinp{
                        position: absolute;
                        left: 0;
                        // top: 0;
                        opacity: 0;
                        width: 80px;
                        height: 80px;
                        cursor: pointer;
                    }
                    .img{
                        width: 80px;
                        height: 80px;
                        cursor: pointer;
                    }

                }
                .img-box:hover{
                    opacity: 0.8;
                }
                .tips{
                    color: #999;
                    display: inline-block;
                    vertical-align: middle;
                    padding-left: 20px;
                    width: calc(100% - 122px);
                    line-height: 1.6;
                }
            }
        }
    }
    .bottom-btns{
        position: fixed;
        text-align: center;
        width: 100%;
        padding: 15px;
        bottom: 0;
        background-color: #fff;
        .btn{
            width: 100%;
            height: 50px;
            font-size: 14px;
        }
    }
    .crash-dialog{
        .dialog-item{
            text-align: center;
            background: #f8f8f9;
            width: 220px;
            margin: 0 auto;
            .img{
                width: 100%;
                height: 100%;
                padding: 20px;
            }
            .title{
                text-align: center;
                padding-bottom: 20px;
            }
        }

    }
    .float-l{
        float: left;
    }
    .error {
        position: absolute;
        z-index: 200;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: #FFFFFF;
        overflow: hidden;

        .error-tips {
            position: absolute;
            top: 50%;
            left: 50%;
            width: 320px;
            transform: translate(-50%, -50%);
        }
    }

</style>